
import $ from 'jquery'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XImg from 'vux/src/components/x-img/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'


let isAndroid = 0
let browser = {
versions: () => {
let u = navigator.userAgent
return {
mobile: !!u.match(/AppleWebKit.*Mobile.*/), // 是否为移动终端
ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), // ios终端
android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 
}
}
}
browser.versions()

export default {
directives: {
TransferDom
},
components: {
banner1: require('./banner1').default,
banner2: require('./banner2').default,
banner3: require('./banner3').default,
XImg,
XHeader,
Swiper,
SwiperItem,
Popup,
Flexbox,
FlexboxItem,
Clocker,
XDialog
},
data () {
return {
firstVisibility: false,

drawerVisibility: false,
bannerList: [
'static/home/banner-1.png',
'static/home/banner-2.png',
'static/home/banner-3.png',
'static/home/banner-4.png'
],
bannerIndex: 0,

videoList: [
'static/home/video-zhuomicang.png',
'static/home/video-xizao.png',
'static/home/video-kunao.png'
],
videos: [
'video-kunao.mp4',
'video-xizao.mp4',
'video-zhuomicang.mp4'
],
videoIndex: 0,

baobaoList: [
'static/home/baobao-1.png',
'static/home/baobao-2.png',
'static/home/baobao-3.png',
'static/home/baobao-4.png',
'static/home/baobao-5.png'
],
baobaoIndex: 0,

showVideoBox: false,
videoSrc: '',

bannerStyle: {
height: 'auto'
},

bannerPopupStatus: false,
banner: '',

yiyuanUrl: 'https://h5.m.taobao.com/awp/core/detail.htm?id=576977040134',

scrollTopPx: 0
}
},
mounted () {
if (new Date('2018-10-08').getTime() - new Date().getTime() > 0) {

}

setTimeout(() => {
this.firstVisibility = true

setTimeout(() => {
$('.btn-img').on('touchstart', function () {
$(this).css({transform: 'scale(0.9)'})
})
$('.btn-img').on('touchend', function () {
$(this).css({transform: 'scale(1)'})
})
}, 300)
}, 300)
},
methods: {
onBannerClick (index) {
if (index === 3) {

} else if (index === 0) {
this.banner = 'banner1'
this.$nextTick(() => {
this.bannerPopupStatus = true
})
} else if (index === 1) {
this.banner = 'banner3'
this.$nextTick(() => {
this.bannerPopupStatus = true
})
} else if (index === 2) {
this.banner = 'banner2'
this.$nextTick(() => {
this.bannerPopupStatus = true
})
}
},
playVideo (mp4) {
this.videoSrc = `static/home/${mp4}`
this.showVideoBox = true
setTimeout(() => {
$('#video-1')[0].currentTime = 0.5
if (isAndroid === 1) {
$('#video-1')[0].play()
} else {
setTimeout(() => {
$('#video-1')[0].play()
}, 500)
}
}, 300)
},
scrollTop () {
$(window).scrollTop(0)
},
goYiyuan () {
if (window.is_wx()) {
let a = new window.ClipboardJS('.yiyuan')
console.log(a)
this.$vux.toast.text(`<span style="font-size: 30px;">链接已复制，请在其他浏览器中打开</span>`)
} else {
window.location.href = this.yiyuanUrl
}
},
onBannerShow () {
$('body').css({
'overflow-y': 'hidden',
height: '100vh'
})
this.scrollTopPx = $(window).scrollTop()
},
onBannerHide () {
$('body').css({
'overflow-y': 'auto',
height: 'auto'
})
$(window).scrollTop(this.scrollTopPx)
}
}
}

window.videoUpdate = function (event) {
let miao = 100
if (event.src.includes('zbyrsp')) {
miao = 22
} else if (event.src.includes('hxsp')) {
miao = 36
} else if (event.src.includes('video-kunao')) {
miao = 49
} else if (event.src.includes('video-xizao')) {
miao = 37
} else if (event.src.includes('video-zhuomicang')) {
miao = 21
}
if (event.currentTime >= miao) {
this.showVideoBox = false
}
}



